.blink {
  position: fixed; 
  top: 0; 
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: tomato;

  justify-content: center;
  align-items: center;
  text-align: center;
  
  animation-duration: 100ms;
  animation-name: blink;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes blink {
  from {
     opacity: 1;
     color: white;
  }
  to {
     opacity: 0;
     color: tomato;
  }
}